#data .slide {
  padding-top: 40%;
  color: white;
  font-size: 1em;
}

#data .slide .item-1, #data .slide .item-2, #data .slide .item-3 {
  margin-top: 50px;
  position: absolute;
  display: block;
  top: 2em;
  width: 60%;
  font-size: 2em;
  -webkit-animation-duration: 35s;
          animation-duration: 35s;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

#data .slide .item-1 {
  -webkit-animation-name: anim-1;
          animation-name: anim-1;
}

@-webkit-keyframes anim-1 {
  0%, 8.3% {
    left: -100%;
    opacity: 0;
  }
  8.3%, 25% {
    left: 2%;
    opacity: 1;
  }
  33.33%, 100% {
    left: 50%;
    opacity: 0;
  }
}

@keyframes anim-1 {
  0%, 8.3% {
    left: -100%;
    opacity: 0;
  }
  8.3%, 25% {
    left: 2%;
    opacity: 1;
  }
  33.33%, 100% {
    left: 50%;
    opacity: 0;
  }
}

#data .slide .item-2 {
  -webkit-animation-name: anim-2;
          animation-name: anim-2;
}

@-webkit-keyframes anim-2 {
  0%, 33.33% {
    left: -100%;
    opacity: 0;
  }
  41.63%, 58.29% {
    left: 2%;
    opacity: 1;
  }
  66.66%, 100% {
    left: 50%;
    opacity: 0;
  }
}

@keyframes anim-2 {
  0%, 33.33% {
    left: -100%;
    opacity: 0;
  }
  41.63%, 58.29% {
    left: 2%;
    opacity: 1;
  }
  66.66%, 100% {
    left: 50%;
    opacity: 0;
  }
}

#data .slide .item-3 {
  -webkit-animation-name: anim-3;
          animation-name: anim-3;
}

@-webkit-keyframes anim-3 {
  0%, 66.66% {
    left: -100%;
    opacity: 0;
  }
  74.96%, 91.62% {
    left: 2%;
    opacity: 1;
  }
  100% {
    left: 50%;
    opacity: 0;
  }
}

@keyframes anim-3 {
  0%, 66.66% {
    left: -100%;
    opacity: 0;
  }
  74.96%, 91.62% {
    left: 2%;
    opacity: 1;
  }
  100% {
    left: 50%;
    opacity: 0;
  }
}

#data .flex {
  display: -ms-flexbox;
  display: flex;
}

#data .flex button {
  -ms-flex: 1;
      flex: 1 1;
  margin-top: 50%;
  background-color: white;
  height: 5vh;
  border: none;
  color: #27b9d2;
  font-size: 1.2em;
  font-weight: 500;
  border-top-right-radius: 25px;
  border-bottom-right-radius: 25px;
  cursor: pointer;
}

#data .flex button:hover {
  -webkit-transition: 0.9s;
  -o-transition: 0.9s;
  transition: 0.9s;
  background-color: #27b8d200;
  border: white solid 1px;
  color: white;
}


